.loading .wrapLoad{
    position: absolute;
    top: 55%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    width: 15vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-content: center;
            align-content: center;
}

.loading .wrapLoad div{
    height: 0;
    width: 1vh;
    background-color: black;
    -webkit-animation: loadingA .8s infinite both;
            animation: loadingA .8s infinite both;
    border-radius: 20%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
.loading .wrapLoad div:nth-of-type(2){
    -webkit-animation-delay: .1s;
            animation-delay: .1s;
}
.loading .wrapLoad div:nth-of-type(3){
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
}
.loading .wrapLoad div:nth-of-type(4){
    -webkit-animation-delay: .3s;
            animation-delay: .3s;
}
.loading .wrapLoad div:nth-of-type(5){
    -webkit-animation-delay: .4s;
            animation-delay: .4s;
}
.loading .wrapLoad div:nth-of-type(6){
